import React from 'react';
import Link from 'gatsby-link';

const SecondPage = ({ data: { allStarWarsPeopleJson: { edges } } }) => {
  return (
    <div>
      {edges.map(edge => {
        return <div key={edge.node.name}>{edge.node.name}</div>;
      })}
      <Link to="/">Go back to the homepage</Link>
    </div>
  );
};

export default SecondPage;

export const query = graphql`
  query StarWarsQuery {
    allStarWarsPeopleJson {
      edges {
        node {
          name
          height
          mass
          hair_color
          skin_color
          eye_color
        }
      }
    }
  }
`;



